import * as React from "react";
import { List, ListItem } from 'material-ui/List';
import ActionGrade from 'material-ui/svg-icons/action/grade';
import ContentInbox from 'material-ui/svg-icons/content/inbox';
import ContentDrafts from 'material-ui/svg-icons/content/drafts';
import ContentSend from 'material-ui/svg-icons/content/send';
import Subheader from 'material-ui/Subheader';
import Link from "gatsby-link";

export default class DocsNav extends React.Component<{}, {}> {

    state = {
        open: false,
    };

    handleToggle = () => {
        this.setState({
            open: !this.state.open,
        });
    };

    handleNestedListToggle = (item) => {
        this.setState({
            open: item.state.open,
        });
    };

    render() {
        return (
            <div id='docs-nav'>
                <List>
                    <ListItem 
                        primaryText="Overview" 
                        containerElement={<Link to="/overview/" />}/>
                </List>
                <ListItem
                    primaryText="Guides"
                    nestedItems={[
                        <ListItem
                            key={1}
                            primaryText="Getting Started"
                            nestedItems={[
                                <ListItem 
                                    key={1} 
                                    primaryText="Introduction to Docs"
                                    containerElement={<Link to="/docs/" />}
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Glossary"
                                />,

                            ]}
                        />,

                        <ListItem
                            key={2}
                            primaryText="User Overview"
                            nestedItems={[
                                <ListItem
                                    key={1}
                                    primaryText="Overview"
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Init Overview"
                                />,
                                <ListItem
                                    key={3}
                                    primaryText="Password Escrow"
                                />,
                                <ListItem
                                    key={4}
                                    primaryText="User Syncing"
                                />,
                                <ListItem
                                    key={5}
                                    primaryText="Generating"
                                />,
                            ]}
                        />,
                        <ListItem
                            key={3}
                            primaryText="Document"
                            nestedItems={[
                                <ListItem
                                    key={1}
                                    primaryText="Overview"
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Encrypt"
                                />,
                                <ListItem
                                    key={3}
                                    primaryText="Decrypt"
                                />,
                                <ListItem
                                    key={4}
                                    primaryText="Update Encrypted Data"
                                />,
                            ]}
                        />,
                        <ListItem
                            key={4}
                            primaryText="Storage"
                            nestedItems={[
                                <ListItem
                                    key={1}
                                    primaryText="Overview"
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Document Store"
                                />,
                            ]}
                        />,
                        <ListItem
                            key={5}
                            primaryText="Groups"
                            nestedItems={[
                                <ListItem
                                    key={1}
                                    primaryText="Introduction"
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Create and Manage Groups"
                                />,
                                <ListItem
                                    key={3}
                                    primaryText="Add and Remove Memebers from a Group"
                                />,
                                <ListItem
                                    key={4}
                                    primaryText="Administrtive Role"
                                />,
                                <ListItem
                                    key={5}
                                    primaryText="Group Creator"
                                />,
                            ]}
                        />,
                        <ListItem
                            key={6}
                            primaryText="Device"
                            nestedItems={[
                                <ListItem
                                    key={1}
                                    primaryText="Overview"
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Authorize"
                                />,
                                <ListItem
                                    key={3}
                                    primaryText="Deauthorize"
                                />,
                            ]}
                        />,
                        <ListItem
                            key={7}
                            primaryText="Sharing"
                            nestedItems={[
                                <ListItem
                                    key={1}
                                    primaryText="Overview"
                                />,
                                <ListItem
                                    key={2}
                                    primaryText="Sharing Docsuments"
                                />,
                                <ListItem
                                    key={3}
                                    primaryText="Sharing with Users"
                                />,
                                <ListItem
                                    key={4}
                                    primaryText="Sharing with Users' Devices"
                                />,
                                <ListItem
                                    key={5}
                                    primaryText="Sharing with Groups"
                                />,
                            ]}
                        />,
                    ]}
                />
            </div>
        );
    }
}


